// Here you can add other styles


.input-group-addon{
	min-width: 62px;
	display: block;
	background: $gray-shine;
}

.form-control:disabled{
	background: $gray-shine;
	
	&::placeholder{
		color: #607d8b;
	}
}

.logo{
	max-width: 200px;
}

[class*='col-']{
	float: left;
}

table{ 
	a:hover{
		text-decoration: none;
	}
	
	i{
		font-size: 16px;
		margin: 5px;
	}
	
	th{
		font-weight: 400;
		color: $brand-primary;
		padding: 5px 15px 5px 5px !important;
	}
	
	td{
		padding: 5px 15px 5px 5px !important;
	}
	
	.r-align{
		text-align: right;
	}
	
	.c-align{
		text-align: center;
	}
}


.tab-content{
	border:0;
	
	.tab-pane{
		padding: 0;
		
		.nopadding{
			padding-left: 0;
		}
	}
}

.nopadding{
	padding-left: 0;
}

.sidebar{
	background: #fff;
	border-right: 1px solid #ccc;
}

.sidebar .nav .nav-item .nav-link, .sidebar .nav .nav-item .navbar .dropdown-toggle, .navbar .sidebar .nav .nav-item .dropdown-toggle{
	color: #333;
}

.sidebar .nav .nav-item.nav-dropdown.open a.nav-link, .sidebar .nav .nav-item.nav-dropdown.open .navbar a.dropdown-toggle, .navbar .sidebar .nav .nav-item.nav-dropdown.open a.dropdown-toggle, .sidebar .nav .nav-item .nav-link.active, .sidebar .nav .nav-item .navbar .active.dropdown-toggle, .navbar .sidebar .nav .nav-item .active.dropdown-toggle{
	color: $gray;
	background: $gray-shine;
}

.sidebar .nav .nav-item .nav-link.active{
	background: $brand-primary !important;
	color: #fff;
	
	i{
		color: #fff;
	}
}


.form-group{
	h4{
		color: $brand-primary;
		font-size: 22px;
		font-weight: 300;
	}
}

.card-header, .card-footer{
	background: transparent;
}

/*
.nav-tabs .nav-link.active:focus, .nav-tabs .navbar .active.dropdown-toggle:focus, .navbar .nav-tabs .active.dropdown-toggle:focus, .nav-tabs .nav-link.active{
	background: $brand-primary;
	color: #fff;
}*/

li.nav-item.active {
    background: $brand-primary;
    //border: 1px solid $gray-light;
	
    a{
		color: #fff !important;
	}
}

.nav-tabs .nav-link{
	border-color: $gray-lighter;
}

.card{
	
	
	.card-header{ 
	
		strong {
			font-weight: 400;
		}
	}
}

.app-footer, .table-striped tbody tr:nth-of-type(odd){
	background: $gray-shine;
	
	p{
		margin-bottom: 0;
		padding-bottom: 0;
	}
}

.onboard-card{
	border: 0;
	background: transparent;
	
	h1{
		font-weight: 700;
	}
	h3{
		font-size: 20px;
		font-weight: 300;
	}
	
	.icon{
		height: 250px;
	}
}



.line-hr{
	border-bottom: 1px dotted $gray-lighter;
	margin: 5px;
}

.remodal-content{
	display: flex;
	align-items: center;

	i{
		    font-size: 50px;
			vertical-align: middle;
			margin-right: 10px;
			color: $brand-primary;
			float: left;
	}
}


select.input-group-addon.form-control {
    border: 1px solid #ccc;
}

caption { 
    display: table-caption;
}

.radio label {
    margin: 15px 15px 0px 0;
	
	input[type=radio]{
		margin-right: 5px;
		vertical-align: middle;
	}
}

.app{
	background: #f5f5f5;
}

.app.login{
	background: #fff;
}

.btn.pull-right {
    margin-top: -6px;
}

.primary-header{
	background: $brand-primary;
	.radio{
		color: #fff;
	}
	
}


.form-control-danger{
	border-color: rgba(255,0,0,0.5);
}

.form-control-warning{
	border-color: rgba(255,152,0,0.5);
}

form-control-success{
	border-color: rgba(0,128,0,0.5);
}

label + .form-control-danger{
	color: rgba(255,0,0,1);
}

label + .form-control-warning{
	color: rgba(255,152,0,1);
}

label + .form-control-success{
	color: rgba(0,128,0,1);
}

label.error{
	color: red;
	font-size: 12px;
	float: right;
}

.table{
	background: #fff;
	
	.thead-default{
		tr{
			
			
			th{
				color: $brand-primary;
				background: #fff;
				border: 0;
			}
		}
	}
}

.social-box{
	i {
		display: block;
		margin: -1px -1px 0;
		font-size: 40px;
		line-height: 80px;
		background: #cfd8dc;
	}
	ul{
		padding: 0px;
		border-top: 1px solid #fff;
		
		li{
			span{
				color: #fff;
			}
		}
	}
}

.dropdown-item.hover{
	background-color: #eceff1; 
}

label.switch.switch-icon.switch-pill{
	display: block;
}

.inbox-nav{
	width: 20%;
	padding: 0px;
	
	.nav-item{
		display: block;
		width: 100%;
		border-bottom: 0px solid $gray-lighter;
		
		a{
			color: $gray-dark;
			
			&.active{
				color: $brand-primary;
				font-size: 1rem;
			}
			
			&:hover{
				background: $gray-shine;
			}
		}
		
		button{
			margin: 10px;
			width: 90%;
		}
	}
}

.inbox-details{
	width: 80%;
	border-left: 0;
	padding: 0;
	
	.card-block{
		padding: 0;
	}
	
	.card-header{
		padding: 15px 5px 10px;
	}
	
	table{
		tr{
			background: $gray-shine;
			cursor: pointer;
			
			&:first-child{
				td{
					border-top: 0;
				}
			}
			&.unread{
				background: #fff;
				font-weight: bold;
			}
			
			td:first-child{
				width: 25px;
				padding: 8px 15px !important;
			}
		}
	}
}

/* RTL support */

*[dir="rtl"]{
	[class*='col-']{
		float: right;
	}
	
	.sidebar .sidebar-nav .nav .nav-item .nav-link [class^="icon-"]{
		margin: 0;
	}
	
	.sidebar .sidebar-nav .nav .nav-item .nav-link i{
		margin: 0 0 0 0.5rem;
	}

	.ml-auto {
		margin-right: auto !important;
		margin-left: 0 !important;
	}
	
	.card-header .card-actions{
		left: 0;
		
		a{
			border: 0;
			border-right: 1px solid #cfd8dc;
		}
	}
}
